<script>
import "../styles/Profile.scss";

import EditProfileForm from "./EditProfileForm.vue";
import editIcon from "vue-material-design-icons/PencilCircleOutline.vue";
import uploadIcon from "vue-material-design-icons/Upload.vue";

import Controller from "@/middleware/controller";
let controller = new Controller();

export default {
    components: { EditProfileForm, editIcon, uploadIcon },
    props: {
        userId: Number,
        user: Object,
    },
    data() {
        return {
            path: process.env.VUE_APP_BACKPATH,
            editMode: false,
            localUser: localStorage.getItem("userId"),
        };
    },
    methods: {
        editProfile() {
            this.editMode = !this.editMode;
        },
        uploadProfilePicture(e) {
            const image = e.target.files[0];
            controller.updateProfilePicture(image, parseInt(this.userId));
            this.updateProfile();
        },
        setAvatarPath() {
            let src = `${this.path}/images/${
                this.user.avatar ?? "undefined.png"
            }`;
            return src;
        },
        setEmail(mail) {
            return `mailto:${mail}`;
        },
        updateProfile() {
            this.emit("profile-edited");
        },
    },
};
</script>

<template id="ProfilePage">
    <div class="profile-block">
        <form class="profile-image" enctype="multipart/form-data">
            <div class="edit-layer"><upload-icon /></div>
            <label for="file-input" class="input-label"
                ><img :src="setAvatarPath()"
            /></label>
            <input
                type="file"
                class="uploadProfilePicture"
                id="file-input"
                accept="image/*"
                v-on:change="(e) => uploadProfilePicture(e)"
                name="file-input"
            />
        </form>
        <div class="profile-data">
            <div class="profile-content" :class="{ editMode: editMode }">
                <h1>{{ this.user.firstname }} {{ this.user.lastname }}</h1>
                <h2>{{ this.user.position }}</h2>
                <p class="profile-contact">
                    <b>Contact : </b>
                    <a :href="setEmail(this.user.email)">{{
                        this.user.email
                    }}</a>
                </p>
            </div>
            <EditProfileForm
                v-bind:user="this.user"
                v-bind:editMode="this.editMode"
                @profile-edited="updateProfile"
            />
            <div
                v-if="this.localUser == this.user.id"
                class="profile-button edit switch"
                :class="editMode"
                v-on:click="editProfile()"
            >
                <edit-icon />Éditer mon profil
            </div>
        </div>
    </div>
</template>
